button {
  border: none;
  padding: 0;
  margin: 0;
  text-decoration: none;
  background: none;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
}

button:focus {
  outline: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}
