.dividers-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.dividers-block__group {
  margin-bottom: 16px;
}

.dividers-block__group:last-child {
  margin-bottom: 0;
}

.dividers-block__label {
  margin-bottom: 12px;
  font-size: 12px;
  color: #888888;
  text-align: center;
}

.dividers-block__indicators-panel {
  display: flex;
}

.dividers-block__indicator {
  margin-right: 1.2vw;
}

.dividers-block__indicator:last-child {
  margin-right: 0;
}

@media only screen and (max-width: 950px) {
  .dividers-block__group {
    width: 100%;
    max-width: 400px;
  }
  .dividers-block__indicators-panel {
    justify-content: space-between;
  }
}
